.loader {
    position: absolute;
    margin: 37vh auto 0 auto;
    left: 0;
    right: 0;
    z-index: 10;
    border: 16px solid #f3f3f3;
    border-top: 16px solid #0064EB;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
}
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}