.header {
    display: inline-flex;
    background-color: #0064EB;
    /* background-color: red; */
    align-items: center;
}

.header img {
    color: #0064EB;
    margin: 16px 22px 16px 41px;
}

.header input {
    width: 500px;
    height: 40px;
    border-radius: 6px;
    background: white url(./../../images/findicon.png) 20px 15px no-repeat;
    background-size: 14px;
    padding-left: 44px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    border: 1px solid #0064EB;
}